.flash {
  width: 50%;
  margin: 0 auto;
  margin-top: 5px;
  padding: 5px;
  position: relative;
  z-index: 10;
  border-radius: 5px;
  display: flex;

  &__text {
    flex: 1;
    padding-left: 15px;
    color: $white;
    font-weight: 600;
    padding: 15px 10px;
  }
  
  &__remove {
    background: none;
    border: none;

    &:hover {
      font-size: 1.1rem;
      color: $button-color-hover;
    }
  }

  &--success {
    background: $green;
  }

  &--info {
    background: $yellow;
  }

  &--error {
    background: $red;
  }
}



